<template>
    <div id="index">
        <div class="left"></div>
        <div class="right"></div>
        <div class="center-btn" @click="openDoor" v-show="showBtn">认识一下吧</div>
        <div class="center">
            <div id="me" v-show="showMe">我</div>
            <div class="img xtu-img">
                <img src="../assets/xtu.jpg" alt="">
            </div>
            <div class="img zjsj-img" v-show="showImg_01">
                <img src="../assets/zjsj.jpg" alt="">
            </div>
            <div class="cxy-img" v-show="showImg_02">
                <div class="txt" v-show="showText">别犹豫啦，不想做程序猿的土木人不是好攻城狮</div>
                <img src="../assets/cxy_02.jpg" alt="">
            </div>
            <div class="img sw-img" v-show="showImg_03">
                <span v-show="showText_01">湖南赛维软件技术有限公司</span>
            </div>
            <div class="img hp-img" v-show="showImg_04">
                <img src="../assets/hp.jpg" alt="">
            </div>
            <div class="img gtja-img" v-show="showImg_05">
                <img src="../assets/gtja.jpg" alt="">
            </div>
            <div class="more-btn" v-show="showMoreBtn">了解更多</div>
            <div class="content">
                <p v-for="(item, index) in content" :key="index">{{item}}</p>
            </div>
        </div>
    </div>
</template>

<script>
    import Velocity from 'velocity-animate'
    export default {
        name: 'index',
        data () {
            return {
                showBtn: true,
                showMoreBtn: false,
                showImg_01: false,
                showImg_02: false,
                showImg_03: false,
                showImg_04: false,
                showImg_05: false,
                showText: false,
                showText_01: false,
                showMe: false,
                content: [],
            }
        },
        mounted () {

        },
        methods: {
            _initAnime () {
                let that = this
                let t1 = this.$anime.timeline({
                    easing: 'easeInOutQuad',
                    duration: 750
                })
                t1.add({
                    targets: '.xtu-img',
                    width: 450,
                    height: 278,
                    duration: 3000,
                    complete: function () {
                        that.content = ['毕业院校：湘潭大学', '就读时间：2010-2014', '专业：土木工程']
                    }
                })
                .add({
                    targets: '.xtu-img',
                    left: '10%',
                    width: 170,
                    height: 96,
                    duration: 2000,
                    delay: 2000,
                    complete: function () {
                        that.showMe = true
                        that.content = []
                    }
                })
                .add({
                    targets: '#me',
                    left: '90%',
                    duration: 5000,
                    complete: function () {
                        that.showImg_01 = true
                    }
                })
                .add({
                    targets: '.zjsj-img',
                    width: 450,
                    height: 278,
                    duration: 3000,
                    complete: function () {
                        that.content = ['公司名称：中建三局装饰有限公司', '在职时间：2014.7 - 2015.5', '职位：施工员']
                    }
                })
                .add({
                    targets: '.zjsj-img',
                    left: '90%',
                    width: 170,
                    height: 96,
                    duration: 2000,
                    delay: 2000,
                    complete: function () {
                        that.content = []
                    }
                })
                .add({
                    targets: '#me',
                    left: '47%',
                    duration: 3000,
                    complete: function () {
                        that.showImg_02 = true
                        that.$anime({
                            targets: '#me',
                            keyframes: [
                                {translateY: -40},
                                {translateX: 100},
                                {translateY: 40},
                                {translateX: 0},
                                {translateX: -100},
                                {translateY: -40},
                                {translateX: 0},
                                {translateY: 0}
                            ],
                            duration: 4000,
                            easing: 'easeOutElastic(1, .8)',
                            loop: 2,
                            complete: function () {
                                that.showText = true
                            }
                        })
                    }
                })
                .add({
                    targets: '#me',
                    top: '47%',
                    duration: 3000,
                    delay: 10000,
                    complete: function () {
                        that.showText = false
                        that.showImg_03 = true
                    }
                })
                .add({
                    targets: '.sw-img',
                    width: 450,
                    height: 278,
                    duration: 3000,
                    complete: function () {
                        that.showText_01 = true
                        that.content = ['公司名称：湖南赛维软件', '在职时间：2015.10 - 2016.8', '职位：web前端开发工程师']
                    }
                })
                .add({
                    targets: '.sw-img',
                    top: '43%',
                    left: '10%',
                    width: 170,
                    height: 96,
                    fontSize: '12px',
                    duration: 2000,
                    delay: 2000,
                    complete: function () {
                        that.content = []
                    }
                })
                .add({
                    targets: '#me',
                    top: '47%',
                    left: '10%',
                    duration: 3000,
                    complete: function () {
                        that.showImg_02 = false
                        that.showImg_04 = true
                    }
                })
                .add({
                    targets: '.hp-img',
                    width: 450,
                    height: 278,
                    duration: 3000,
                    complete: function () {
                        that.content = ['公司名称：上海惠普', '在职时间：2016.8 - 2018.5', '职位：开发工程师']
                    }
                })
                .add({
                    targets: '.hp-img',
                    top: '43%',
                    left: '50%',
                    width: 170,
                    height: 96,
                    duration: 2000,
                    delay: 2000,
                    complete: function () {
                        that.content = []
                    }
                })
                .add({
                    targets: '#me',
                    top: '47%',
                    left: '47%',
                    duration: 3000,
                    complete: function () {
                        that.showImg_05 = true
                    }
                })
                .add({
                    targets: '.gtja-img',
                    width: 450,
                    height: 278,
                    duration: 3000,
                    complete: function () {
                        that.content = ['公司名称：国泰君安证券', '在职时间：2018.5 - 至今', '职位：web前端开发工程师']
                    }
                })
                .add({
                    targets: '.gtja-img',
                    top: '43%',
                    left: '90%',
                    width: 170,
                    height: 96,
                    duration: 2000,
                    delay: 2000,
                    complete: function () {
                        that.content = []
                    }
                })
                .add({
                    targets: '#me',
                    top: '47%',
                    left: '90%',
                    duration: 3000,
                    complete: function () {
                        that.showMe = false
                        that.showMoreBtn = true
                        that.$anime({
                            targets: '.xtu-img',
                            keyframes: [
                                {left: '50%', top: '5%'},
                                {left: '90%'},
                                {top: '43%'},
                                {left: '50%'},
                                {left: '10%'},
                                {top: '5%'}
                            ],
                            loop: true,
                            duration: 10000,
                            easing: 'easeOutElastic(1, .8)'
                        })
                        that.$anime({
                            targets: '.zjsj-img',
                            keyframes: [
                                {top: '43%'},
                                {left: '50%'},
                                {left: '10%'},
                                {top: '5%'},
                                {left: '50%'},
                                {left: '90%'}
                            ],
                            loop: true,
                            duration: 10000,
                            easing: 'easeOutElastic(1, .8)'
                        })
                        that.$anime({
                            targets: '.sw-img',
                            keyframes: [
                                {top: '5%'},
                                {left: '50%'},
                                {left: '90%'},
                                {top: '43%'},
                                {left: '50%'},
                                {left: '10%'}
                            ],
                            loop: true,
                            duration: 10000,
                            easing: 'easeOutElastic(1, .8)'
                        })
                        that.$anime({
                            targets: '.hp-img',
                            keyframes: [
                                {left: '10%'},
                                {top: '5%'},
                                {left: '50%'},
                                {left: '90%'},
                                {top: '43%'},
                                {left: '50%'}
                            ],
                            loop: true,
                            duration: 10000,
                            easing: 'easeOutElastic(1, .8)'
                        })
                        that.$anime({
                            targets: '.gtja-img',
                            keyframes: [
                                {left: '50%'},
                                {left: '10%'},
                                {top: '5%'},
                                {left: '50%'},
                                {left: '90%'},
                                {top: '43%'}
                            ],
                            loop: true,
                            duration: 10000,
                            easing: 'easeOutElastic(1, .8)'
                        })
                    }
                })
            },
            openDoor () {
                let that = this
                let leftDom = document.getElementsByClassName('left')[0]
                let rightDom = document.getElementsByClassName('right')[0]
                that.showBtn = false
                this.$anime({
                    targets: '.center',
                    backgroundColor: '#FFF',
                    easing: 'easeInOutQuad',
                    duration: 3000
                })
                Velocity(leftDom, {
                    left: "-35%"
                }, {
                    duration: 3000,
                    complete: function () {
                        that._initAnime()
                    }
                })
                Velocity(rightDom, {
                    right: "-35%"
                }, {
                    duration: 3000
                })
            }
        }
    }
</script>

<style lang="less" scoped>
    #index {
        position: relative;
        width: 100%;
        height: 100%;
        .left {
            position: absolute;
            top: 0;
            left: 0;
            width: 50%;
            height: 100%;
            background-color: greenyellow;
            z-index: 99;
        }
        .right {
            position: absolute;
            right: 0;
            top: 0;
            width: 50%;
            height: 100%;
            background-color: goldenrod;
            z-index: 99;
        }
        .center-btn {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 200px;
            height: 200px;
            background-color: pink;
            border-radius: 50%;
            line-height: 200px;
            text-align: center;
            font-size: 24px;
            cursor: pointer;
            z-index: 100;
        }
        .more-btn {
            position: absolute;
            bottom: 30px;
            left: 50%;
            transform: translateX(-50%);
            width: 100px;
            height: 100px;
            background-color: #52b2ff;
            color: #fff;
            border-radius: 50%;
            line-height: 100px;
            text-align: center;
            font-size: 16px;
            cursor: pointer;
            z-index: 100;
        }
        .center {
            position: absolute;
            top: 0;
            left: 15%;
            right: 15%;
            bottom: 0;
            background-color: #000;
            #me {
                position: absolute;
                top: 10%;
                left: 5%;
                width: 50px;
                height: 50px;
                border-radius: 50%;
                background-color: rgb(24, 255, 116);
                line-height: 50px;
                text-align: center;
                font-size: 16px;
            }
            .img {
                position: absolute;
                top: 5%;
                left: 50%;
                transform: translateX(-50%);
                width: 1px;
                height: 1px;
                img {
                    width: 100%;
                    height: 100%;
                }
                &.sw-img {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    border: 1px solid #000;
                    background: #fff;
                    font-size: 20px;
                }
            }
            .cxy-img {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                width: 141px;
                height: 110px;
                .txt {
                    position: absolute;
                    top: -50px;
                    left: -30px;
                    width: 280px;
                }
                img {
                    width: 100%;
                    height: 100%;
                }
            }
            .content {
                position: absolute;
                bottom: 0;
                left: 0;
                width: 100%;
                height: 200px;
                padding: 10px;
                font-size: 20px;
                text-align: center;
                font-weight: 700;
            }
        }
    }
</style>